.backdrop {
  width: 100%;
  height: 100%;
  visibility: visible;
  pointer-events: painted;
  opacity: 1;
  z-index: 2;
  background: rgba(0, 0, 0, .2);
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s, visibility .25s;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop__modal {
  width: 95%;
  max-width: 280px;
  height: 885px;
  opacity: 1;
  background-color: tomato;
  border-radius: 25px;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 768px) {
  .backdrop__modal {
    max-width: 704px;
    height: 550px;
    top: 50%;
  }
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-hidden__modal {
  opacity: 0;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s, visibility .25s;
  transform: translate(-50%, -50%)scale(0);
}

.backdrop__button {
  width: 30px;
