  background-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  margin-top: 8px;
  margin-right: 8px;
  padding: 0;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.backdrop__icon {
  color: #000;
}

